<template>
  <base-material-card
    color="btn_primary"
    icon="mdi-access-point"
  >
    <template #after-heading>
      <span
        :class="
          $vuetify.theme.dark
            ? 'btn_primary--text font-spartan'
            : 'btn_primary--text font-spartan'
        "
      >
        <!-- {{ computudTitle }} -->
        <!-- {{ $t("index_class_data_table") }} -->
        {{ computudTitle }}
      </span>
    </template>
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-btn
            class="mr-1"
            outlined
            rounded
            small
            dark
            color="primary"
            @click="refresh"
          >
            <v-tooltip
              color="primary"
              bottom
            >
              <template #activator="{ on, attrs }">
                <v-icon
                  v-bind="attrs"
                  color="size__icon_refresh"
                  v-on="on"
                >
                  mdi-cached
                </v-icon>
              </template>
              <span class="font-spartan-small">
                {{ $t("Segarkan") }}
              </span>
            </v-tooltip>
          </v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          cols="12"
          md="4"
          class="mt-2"
        >
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            color="primary"
            dense
          />
        </v-col>

        <v-col
          cols="12"
          md="8"
          class="d-flex flex-row-reverse"
        >
          <div class="ml-2">
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="btn_primary"
                  dark
                  v-bind="attrs"
                  outlined
                  v-on="on"
                >
                  <v-icon>mdi-chevron-down</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item-group>
                  <v-list-item>
                    <v-list-item-action>
                      <v-btn
                        width="400"
                        outlined
                        color="btn_primary"
                      >
                        Export
                      </v-btn>
                    </v-list-item-action>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </div>
          <v-btn
            v-if="studio.bank"
            outlined
            :disabled="studio.bank.length === 0"
            color="btn_primary"
            class="ml-2"
            @click="upDialog"
          >
            <v-icon> mdi-plus </v-icon>
            Add Class
          </v-btn>

          <v-btn
            :disabled="selected.length === 0"
            class="ml-2"
            outlined
            color="blue"
            @click="upDialogApproves(selected)"
          >
            <v-icon> mdi-check-decagram </v-icon>
            Approve {{ selected.length }} item
          </v-btn>
          <v-btn
            :disabled="selected.length === 0"
            outlined
            color="red"
            @click="upDialogDeletes(selected)"
          >
            <v-icon> mdi-delete-empty-outline </v-icon>
            Delete {{ selected.length }} item
          </v-btn>
        </v-col>
        <v-col cols="12">
          <v-data-table
            v-model="selected"
            show-select
            :search="search"
            :headers="headers"
            :items="data"
            mobile-breakpoint="0"
            show-expand
          >
            <!-- Expand -->
            <template #expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      class="d-flex flex-row-reverse"
                    >
                      <v-icon color="blue">
                        mdi-share-variant
                      </v-icon>
                    </v-col>
                    <v-col
                      cols="12"
                      md="3"
                    >
                      <base-material-stats-card
                        v-if="item.student"
                        color="info"
                        icon="mdi-account-multiple-plus-outline"
                        title="Registrant"
                        :value="String(item.student.length)"
                        sub-icon="mdi-clock"
                        sub-text="Just Updated"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="3"
                    >
                      <base-material-stats-card
                        color="info"
                        icon="mdi-twitter"
                        title="Followers"
                        value="+245"
                        sub-icon="mdi-clock"
                        sub-text="Just Updated"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="3"
                    >
                      <base-material-stats-card
                        color="info"
                        icon="mdi-twitter"
                        title="Followers"
                        value="+245"
                        sub-icon="mdi-clock"
                        sub-text="Just Updated"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="3"
                    >
                      <base-material-stats-card
                        color="info"
                        icon="mdi-twitter"
                        title="Followers"
                        value="+245"
                        sub-icon="mdi-clock"
                        sub-text="Just Updated"
                      />
                    </v-col>
                  </v-row>
                </v-container>
                Helo {{ item.name }}
              </td>
            </template>
            <!-- Header -->
            <template #[`header.name`]="{ header }">
              {{ $t(header.text) }}
            </template>
            <template #[`header.levels`]="{ header }">
              {{ $t(header.text) }}
            </template>
            <template #[`header.kapasitas`]="{ header }">
              {{ $t(header.text) }}
            </template>
            <template #[`header.status`]="{ header }">
              {{ $t(header.text) }}
            </template>
            <template #[`header.category.display_name`]="{ header }">
              {{ $t(header.text) }}
            </template>
            <template #[`header.status_kelas`]="{ header }">
              {{ $t(header.text) }}
            </template>

            <!-- Item -->
            <template #[`item.img.url`]="{ item }">
              <v-hover v-slot="{ hover }">
                <v-card
                  :elevation="hover ? 12 : 2"
                  :class="{ 'on-hover': hover }"
                  width="150"
                  color="grey"
                >
                  <v-img
                    v-if="item.img"
                    :src="item.img.url"
                    width="140"
                    height="100"
                    gradient="to top right, rgba(0,0,0,.33), rgba(0,0,0,.7)"
                    style="cursor: pointer"
                    @click="changePict(item)"
                  >
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          class="col__img__class"
                        >
                          <v-btn
                            icon
                            dark
                            :class="{ 'show-btns': hover }"
                            :color="transparent"
                          >
                            <v-icon
                              :class="{ 'show-btns': hover }"
                              :color="transparent"
                            >
                              mdi-camera-flip-outline
                            </v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-img>
                  <v-img
                    v-else
                    src="https://assets.tokopedia.net/assets-tokopedia-lite/v2/zeus/kratos/62cb37c4.png"
                    width="140"
                    height="100"
                    gradient="to top right, rgba(0, 0, 0, 0.05), rgba(20, 20, 20, 0.05)"
                    style="background-color: rgba(0, 0, 0, 0.05)"
                  />
                  <input
                    ref="fileUpload"
                    type="file"
                    style="display: none"
                    accept="image/*"
                    @change="changePictProfile"
                  >
                </v-card>
              </v-hover>
            </template>
            <template #[`item.name`]="{ item }">
              <v-hover v-slot="{ hover }">
                <v-chip
                  color="transparent"
                  :class="{ 'on-hover': hover }"
                  :text-color="
                    hover
                      ? 'btn_primary'
                      : $vuetify.theme.dark
                        ? 'white'
                        : 'black'
                  "
                  style="cursor: pointer"
                  class="mt-6"
                  @click="
                    navigate(
                      `/class/${item.slug}/keyword/${item.keyword}/student`
                    )
                  "
                >
                  {{ item.name }}
                </v-chip>
              </v-hover>
              <div class="bg-hover">
                <div class="d-flex flex-row flex-nowrap">
                  <div>
                    <div class="d-flex flex-column flex-nowrap mt-2">
                      <a
                        class="font-a d-flex flex-nowrap"
                        @click="upDialogeditClass(item)"
                      >
                        <v-tooltip
                          bottom
                          color="blue"
                        >
                          <template #activator="{ on, attrs }">
                            <v-icon
                              small
                              color="blue"
                              class="mr-1"
                              v-bind="attrs"
                              v-on="on"
                            >
                              mdi-pencil
                            </v-icon>
                          </template>
                          <span class="font-spartan-small"> Edit </span>
                        </v-tooltip>
                      </a>
                    </div>
                  </div>
                  <div>
                    <div class="d-flex flex-column mt-2 ml-1">
                      <a
                        class="d-flex blue--text flex-nowrap"
                        @click="upInfoClass(item)"
                      >
                        <v-tooltip
                          color="blue"
                          bottom
                        >
                          <template #activator="{ on, attrs }">
                            <v-icon
                              v-bind="attrs"
                              color="blue"
                              small
                              v-on="on"
                            >
                              mdi-eye
                            </v-icon>
                          </template>
                          <span class="font-spartan-small"> Info </span>
                        </v-tooltip>
                      </a>
                    </div>
                  </div>
                  <div>
                    <div class="d-flex flex-column mt-2 ml-1">
                      <a
                        class="font-a-delete d-flex flex-nowrap"
                        @click="uploadMatery(item)"
                      >
                        <v-tooltip
                          color="btn_primary"
                          bottom
                        >
                          <template #activator="{ on, attrs }">
                            <v-icon
                              v-bind="attrs"
                              color="btn_primary"
                              small
                              v-on="on"
                            >
                              mdi-camera
                            </v-icon>
                          </template>
                          <span class="font-spartan-small"> Upload </span>
                        </v-tooltip>
                      </a>
                    </div>
                  </div>
                  <div>
                    <div class="d-flex flex-column mt-2 ml-1">
                      <a
                        class="font-a-delete d-flex flex-nowrap"
                        @click="hideDataClassesStudio(item)"
                      >
                        <v-tooltip
                          color="red"
                          bottom
                        >
                          <template #activator="{ on, attrs }">
                            <v-icon
                              v-bind="attrs"
                              color="red"
                              small
                              v-on="on"
                            >
                              mdi-eye-off
                            </v-icon>
                          </template>
                          <span class="font-spartan-small white--text">
                            Hided
                          </span>
                        </v-tooltip>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </template>
            <template #[`item.levels`]="{ item }">
              <v-chip
                label
                text-color="white"
                :color="setColorLevels(item.levels)"
                class="text-capitalize chips--weight"
              >
                {{ item.levels }}
              </v-chip>
            </template>
            <template #[`item.status`]="{ item }">
              <v-chip
                :color="setColorStatus(item.status)"
                label
                text-color="white"
                class="chips--weight"
              >
                {{ $t(item.status) }}
              </v-chip>
            </template>
            <template #[`item.kapasitas`]="{ item }">
              <v-chip
                label
                color="btn_primary"
                text-color="white"
              >
                {{ item.student ? item.student.length : "0" }} /
                {{ item.kapasitas }}
              </v-chip>
            </template>
            <template #[`item.status_kelas`]="{ item }">
              <v-chip
                label
                :color="setStatusClass(item.status_kelas)"
                text-color="white"
              >
                {{ item.status_kelas }}
              </v-chip>
            </template>
          </v-data-table>
        </v-col>
      </v-row>
    </v-container>
  </base-material-card>
</template>

<script>
  export default {
    props: {
      data: {
        type: Array,
        default: null,
      },
      studio: {
        type: Object,
        default: null,
      },
    },
    data: () => ({
      headers: [
        {
          text: "#",
          value: "img.url",
          sortable: false,
        },
        {
          text: "table.class.th.name",
          align: "start",
          sortable: false,
          value: "name",
        },
        { text: "table.class.th.levels", value: "levels", sortable: false },
        {
          text: "table.class.th.category",
          value: "category.display_name",
          sortable: false,
        },

        { text: "table.class.th.capacity", value: "kapasitas", sortable: false },
        { text: "table.class.th.status", value: "status", sortable: false },
        {
          text: "table.class.th.status_time",
          value: "status_kelas",
          sortable: false,
        },
      ],
      transparent: "rgba(255, 255, 255, 0)",
      selected: [],
      search: "",
      dialogAdd: {},
      dataItem: {},
      imageUrl: null,
      files: null,
    }),
    computed: {
      computudTitle () {
        let name = "EnsikloLive - All"
        if (this.$route.query.summary === "all") {
          name = "EnsikloLive - All"
        }
        if (this.$route.query.summary === "approved") {
          name = "EnsikloLive - Approved"
        }
        if (this.$route.query.summary === "non-approved") {
          name = "EnsikloLive - Non Approved"
        }
        if (this.$route.query.summary === "new") {
          name = "EnsikloLive - New"
        }
        return name
      },
    },
    mounted () {
    // console.log(this.studio)
    },
    methods: {
      setColorLevels (levels) {
        if (levels === "beginner" || levels === "beginner") return "pallet1"
        if (levels === "advance") return "secondary"
        if (levels === "intermediate") return "btn_primary"
      },
      setColorStatus (status) {
        if (status === "publish") return "btn_primary"
        if (status === "draft") return "blue"
        else return "red"
      },
      setStatusClass (status) {
        if (status === "ongoing") return "primary"
        if (status === "upcoming") return "btn_primary"
        else return "red"
      },
      upDialog () {
        this.$emit("add", { item: this.dialogAdd })
      },
      upDialogDeletes (item) {
        this.$emit("deletes", { item: item })
        this.selected = []
      },
      upDialogApproves (item) {
        this.$emit("approves", { item: item })
        this.selected = []
      },
      upDialogeditClass (item) {
        this.$emit("update", { item: item })
      },
      upDeleteClass (item) {
        this.$emit("del", { item: item })
      },
      upInfoClass (item) {
        this.$emit("info", { item: item })
      },
      uploadMatery (item) {
        this.$emit("upload", { item: item })
      },
      hideDataClassesStudio (item) {
        this.$emit("hide", { item: item })
      },
      refresh () {
        this.$emit("refresh")
      },
      changePict (item) {
        this.$refs.fileUpload.click()
        this.dataItem = item
      },
      navigate (link) {
        this.$router.push(link)
      },
      changePictProfile (event) {
        const files = event.target.files
        const filename = files[0].name
        console.log(filename)
        const fileReader = new FileReader()
        fileReader.addEventListener("load", () => {
          this.imageUrl = fileReader.result
        //   console.log(this.imageUrl)
        })
        fileReader.readAsDataURL(files[0])
        this.files = files[0]

        if (this.files.size > 2000000) {
          console.log("too big")
          const Toast = this.$swal.mixin({
            toast: true,
            position: "bottom-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer)
              toast.addEventListener("mouseleave", this.$swal.resumeTimer)
            },
            popup: "swal2-show",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          })
          Toast.fire({
            icon: "error",
            title: "file too big",
          })
        } else {
          this.$emit("change", {
            item: {
              files: this.files,
              id: this.dataItem.id,
            },
          })
        }
      },
    },
  }
</script>

<style lang="sass" scoped>
// .font-customize
//   color:
.font-a-delete
  color: #4CAF50 !important
.show-btns
  color: rgba(255, 255, 255, 1) !important
.col__img__class
  margin-top: 19px
  margin-left: 40px
</style>
